body {
    font-family: 'Roboto', sans-serif;
    background-color: #f4f4f4;
    color: #333;
    margin: 0;
  }
  
  h1, h2, h3 {
    font-weight: bold;
    color: #005f73; /* Dark Blue */
  }
  
  h1 {
    font-size: 2.5em;
  }
  
  h2 {
    font-size: 1.8em;
  }
  
  .header {
    background-image: url('https://images.app.goo.gl/PvsZJjPYn3hPq2TP6');
    background-size: cover;
    color: white;
    padding: 50px 0;
    text-align: center;
  }
  
  .header h1 {
    font-size: 3em;
    margin-bottom: 10px;
  }
  
  .header .cta-button {
    background-color: #fca311; /* Bright Orange */
    padding: 15px 30px;
    font-size: 1.2em;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 5px;
  }
  
  .footer {
    background-color: #333;
    color: white;
    padding: 20px;
    text-align: center;
  }
  
  .footer a {
    color: #fca311;
    text-decoration: none;
  }
  
  footer .social-icons {
    margin-top: 10px;
  }
  
  .social-icons img {
    width: 40px;
    margin: 0 10px;
  }
  
  button {
    background-color: #fca311;
    padding: 12px 20px;
    border: none;
    color: white;
    font-weight: bold;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #ff7a00;
  }
  